.userList {
  flex: 4;
}
.userListUser {
  display: flex;
  align-items: center;
}
.userListImg {
  width: 32px;
  height: 32px;
  border-radius: 50%;
  object-fit: cover;
  margin-right: 10px;
}
.userListEdit {
  border: none;
  border-radius: 10px;
  padding: 5px 10px;
  background-color: rgb(28, 200, 28);
  cursor: pointer;
  margin-right: 20px;
}
.userListDelete {
  color: red;
  cursor: pointer;
}
