.widgetSm {
  flex: 1;
  box-shadow: -2px 4px 14px 0px rgba(117, 116, 116, 1);
  -webkit-box-shadow: -2px 4px 14px 0px rgba(117, 116, 116, 1);
  -moz-box-shadow: -2px 4px 14px 0px rgba(117, 116, 116, 1);
  padding: 20px;
  margin-right: 20px;
}
.widgetSmTitle {
  font-size: 22px;
  font-weight: bold;
}
.widgetSmImg {
  width: 40px;
  height: 40px;
  border-radius: 50%;
  object-fit: cover;
}
.widgetSmList {
  margin: 0;
  padding: 0;
  list-style: none;
}
.widgetSmallListItem {
  display: flex;
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin: 20px 0px;
}
.widgetSmUser {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.widgetSmUserName {
  font-weight: 600;
}
.widgetSmUserTitle {
  font-weight: 300;
}
.widgetSmButton {
  display: flex;
  align-items: center;
  border: none;
  border-radius: 10px;
  padding: 7px 10px;
  cursor: pointer;
  background-color: rgb(224, 228, 230);
  color: black;
}
.widgetSmIcon {
  font-size: 16px;
  margin-right: 5px;
}
